              //选中换热站且不存在热力所
              si_company_id = this.pri_detail.treeNodeIds[1];//
              si_child_company_id = this.pri_detail.treeNodeIds[2];//
            } else if (count === 4 && this.pri_detail.treeNodeType[2] === '3' && type === '4') {
              //选中换热站，且不存在分公司
              si_company_id = this.pri_detail.treeNodeIds[1];//
              si_institute_id = this.pri_detail.treeNodeIds[2];//
