        this.stationForm.si_address = row.si_address;
        this.treeDataOptions = [];
        if(this.customGlobal.userInfoExt.cui_type !== '0') {
          this.treeDataOptions[0] = this.stationForm.si_company_id;
          if (this.stationForm.si_child_company_id !== '' && this.stationForm.si_institute_id !== '') {
            this.treeDataOptions[1] = this.stationForm.si_child_company_id;
            this.treeDataOptions[2] = this.stationForm.si_institute_id;
          } else if (this.stationForm.si_child_company_id === '' && this.stationForm.si_institute_id !== '') {
            this.treeDataOptions[1] = this.stationForm.si_institute_id;
          } else if (this.stationForm.si_child_company_id !== '' && this.stationForm.si_institute_id === '') {
            this.treeDataOptions[1] = this.stationForm.si_child_company_id;
          }
        }else{
          this.treeDataOptions[0] = "0";
          this.treeDataOptions[1] = this.stationForm.si_company_id;
          if (this.stationForm.si_child_company_id !== '' && this.stationForm.si_institute_id !== '') {
            this.treeDataOptions[2] = this.stationForm.si_child_company_id;
            this.treeDataOptions[3] = this.stationForm.si_institute_id;
          } else if (this.stationForm.si_child_company_id === '' && this.stationForm.si_institute_id !== '') {
            this.treeDataOptions[2] = this.stationForm.si_institute_id;
          } else if (this.stationForm.si_child_company_id !== '' && this.stationForm.si_institute_id === '') {
            this.treeDataOptions[2] = this.stationForm.si_child_company_id;
          }
        }
        this.selectedOptions =[];
        this.selectedOptions[0] = row.si_province;
        this.selectedOptions[1] = row.si_city;
        this.selectedOptions[2] = row.si_country;
        this.stationForm.si_longitude = row.si_longitude;
        this.stationForm.si_latitude = row.si_latitude;
        this.stationForm.si_count_area = row.si_count_area;
        this.stationForm.si_contact_name = row.si_contact_name;
        this.stationForm.si_contact_phone = row.si_contact_phone;
        this.stationForm.si_memo = row.si_memo;
        this.stationForm.si_uuid = row.si_uuid;
        this.stationForm.flag = '2';
        this.stationDialogFormVisible = true;

      },
      handleDeleteRow(index, row) {
        console.log("行号："+index+",行内容：" + JSON.stringify(row) );
        this.stationForm.tci_count = row.tci_count;
        if(row.tci_count !== 0 || row.tpui_count !== 0 ){
          this.$alert('当前要删除换热站[<span style="color:red;font-size: 20px;">' + row.si_station_name + '</span>]的小区或用户数量不为零，不可以删除', '删除提示', {
            dangerouslyUseHTMLString: true
          });
        }else {
          this.$confirm('请再次确认是否要删除[<span style="color:red;">' + row.si_station_name + '</span>]该行记录?', '删除提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            dangerouslyUseHTMLString: true,
            closeOnClickModal: false,
            closeOnPressEscape: false,

          }).then(() => {
            let _jsondata_ =   {
              si_uuid:row.si_uuid,
            } ;
            var p1 = this.customGlobal.postHttpClient(
              this.customGlobal.serverUrl.heatExchangeStation_delete,
              //this.customGlobal.serverUrl.baseURI,
              this.customGlobal.serverUrl.orgBaseURI,
              {"Content-Type": "application/x-www-form-urlencoded"},
              this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
            );
            p1.then((res) => {
              console.log("异步调用成功返回:"+JSON.stringify(res));
              if(res.data.status=='success'){
                this.$message({
                  type: 'success',
                  showClose: true,
                  duration:3000,
                  dangerouslyUseHTMLString: true,
                  message: '<span style="color:black;">基础信息 ['+row.si_station_name+'] 删除成功 </span>'
                });
                Bus.$emit('globalMsg',{curid:row.si_uuid,target:{},method:'3'});
                this.reQueryValveCurrentData();
              }else{
                console.info("删除失败");
                this.$alert(res.data.msg, '删除失败', {
                  confirmButtonText: '确定',
                  callback: action => {
                    console.info('data:'+JSON.stringify(res.data));
                    if(res.data.msg==='请登录认证'){
                      this.$router.push({path:'/login'});
                    }
                  }
                });
              }
            },error=>{
              console.log("异步调用出错:"+JSON.stringify(error));
              this.$alert(JSON.stringify(error.message), '删除出错!', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            }).catch((res) => {
              console.log("异步调用失败返回:"+JSON.stringify(res));
              this.$alert(JSON.stringify(res.message), '删除出错', {
                confirmButtonText: '确定',
                callback: action => {
                }
              });
            });
          }).catch((res) => {
            console.log("取消删除:"+JSON.stringify(res));
            this.$message({
              type: 'info',
              showClose: true,
              duration:3000,
              dangerouslyUseHTMLString: true,
              message: '已取消删除'
            });
          });
        }
      },

      submitForm(formName) {
        if(this.treeDataOptions.length <2){
          this.$alert('所属组织机构不符合规则，请至少选择到热力公司级别', '新增提示!', {
            dangerouslyUseHTMLString: true
          });
          return false;
        }
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this;

            let _jsondata_ =   {
              si_uuid:that.stationForm.si_uuid,
              si_company_id :that.stationForm.si_company_id ,   //
              si_child_company_id :that.stationForm.si_child_company_id ,     //
              si_institute_id :that.stationForm.si_institute_id ,     //
              si_station_name:that.stationForm.si_station_name,
              si_address:that.stationForm.si_address,
              si_province:that.stationForm.si_province,
              si_city:that.stationForm.si_city,
              si_country:that.stationForm.si_country,
              si_count_area:that.stationForm.si_count_area,
              si_contact_name:that.stationForm.si_contact_name,
              si_contact_phone:that.stationForm.si_contact_phone,
              si_longitude:that.stationForm.si_longitude,
              si_latitude:that.stationForm.si_latitude,
              si_memo: that.stationForm.si_memo,
              flag : that.stationForm.flag
            } ;
            const loading = this.$loading({
              lock: true,
              text: '提交中...',
              target: '.main',
              spinner: 'el-icon-loading',

              background: 'rgba(0, 0, 0, 0)'
            });

            if(that.stationForm.flag === '1'){
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.heatExchangeStation_add,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.stationDialogFormVisible =false;
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.stationForm.si_station_name+'] 新增成功 </span>'
                  });
                  if(this.pri_detail.treeNodeType.length === 5) {
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[3],target:{id:that.stationForm.si_uuid,caption:that.stationForm.si_station_name,type:4},method:'1',layer:'2'});
                  }else if(this.pri_detail.treeNodeType.length === 4 && this.pri_detail.treeNodeType[3] ==='3' ) {
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[3],target:{id:that.stationForm.si_uuid,caption:that.stationForm.si_station_name,type:4},method:'1',layer:'2'});
                  }else if(this.pri_detail.treeNodeType.length === 4 && this.pri_detail.treeNodeType[3] ==='4' ) {
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[2],target:{id:that.stationForm.si_uuid,caption:that.stationForm.si_station_name,type:4},method:'1',layer:'2'});
                  }else if(this.pri_detail.treeNodeType.length === 3 && this.pri_detail.treeNodeType[2]==='4') {
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[1],target:{id:that.stationForm.si_uuid,caption:that.stationForm.si_station_name,type:4},method:'1',layer:'2'});
                  }else if(this.pri_detail.treeNodeType.length === 3 && this.pri_detail.treeNodeType[2]==='3') {
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[2],target:{id:that.stationForm.si_uuid,caption:that.stationForm.si_station_name,type:4},method:'1',layer:'2'});
                  }else if(this.pri_detail.treeNodeType.length === 3 && this.pri_detail.treeNodeType[2]==='2') {
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[2],target:{id:that.stationForm.si_uuid,caption:that.stationForm.si_station_name,type:4},method:'1',layer:'2'});
                  }else{
                    Bus.$emit('globalMsg',{curid:this.pri_detail.treeNodeIds[1],target:{id:that.stationForm.si_uuid,caption:that.stationForm.si_station_name,type:4},method:'1',layer:'2'});
                  }

                  this.queryValveCurrentData();
                }else{
                  console.info("新增失败");
                  loading.close();
                  this.$alert(res.data.msg, '新增失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }

              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '新增出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '新增出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
            }else{
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.heatExchangeStation_update,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.stationDialogFormVisible =false;
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.stationForm.si_station_name+'] 修改成功 </span>'
                  });
                  //热力公司增加树节点,1是同级，2是子级
                  //注意：targetNode中的id是自动生成且唯一,如果是创建第1层的热力公司，currentNodeId一定设置为0,layer一定设置为2
                  Bus.$emit('globalMsg',{curid:that.stationForm.si_uuid,target:{id:that.stationForm.si_uuid,caption:that.stationForm.si_station_name,type:4},method:'2'});

                  this.queryValveCurrentData();
                }else{
                  console.info("修改失败");
                  loading.close();
                  this.$alert(res.data.msg, '修改失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }

              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '修改出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '修改出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      removeTreeNode (node, data) {
        const parent = node.parent
        const children = parent.data.detail || parent.data
        const index = children.findIndex(d => d.id === data.id)
        children.splice(index, 1)
      },
      //热力所重新装载树
      repackaging(){
        if( this.customGlobal.userInfoExt.cui_type !== '0' ){
          for(let typeO = 0 ; typeO < this.treeData.length ; typeO++){
            if(this.treeData[typeO] !== undefined && this.treeData[typeO].detail !== undefined ){
              for(let typeT = 0;typeT < this.treeData[typeO].detail.length ;typeT++){
                //删除热力公司下的换热站
                if(this.treeData !== undefined && this.treeData[typeO].detail[typeT].type > 3 ){
                  this.treeData[typeO].detail.splice(typeT,1);
                  typeT--;
                }
                //删除热力分公司下的热力所、换热站
                if(this.treeData !== undefined && this.treeData[typeO].detail !== undefined && this.treeData[typeO].detail[typeT].detail !== undefined){
                  for(let typeS = 0;typeS < this.treeData[typeO].detail[typeT].detail.length ;typeS++) {
                    //删除热力所下的换热站
                    if (this.treeData[typeO].detail[typeT].detail[typeS].type === 3) {
                      delete this.treeData[typeO].detail[typeT].detail[typeS].detail;
                    }
                    //删除热力公司下的换热站
                    if (this.treeData[typeO].detail[typeT].detail[typeS].type === 4) {
                      this.treeData[typeO].detail[typeT].detail.splice(typeS, 1);
                      typeS--;
                    }

                  }
                }
              }
            }
          }
        }else{
          for(let typeO = 0 ; typeO < this.treeData[0].detail.length ; typeO++){
            if(this.treeData[0].detail[typeO] !== undefined && this.treeData[0].detail[typeO].detail !== undefined ){
              for(let typeT = 0;typeT < this.treeData[0].detail[typeO].detail.length ;typeT++){
                //删除热力公司下的换热站
                if(this.treeData[0].detail !== undefined && this.treeData[0].detail[typeO].detail[typeT].type > 3 ){
                  this.treeData[0].detail[typeO].detail.splice(typeT,1);
                  typeT--;
                }
                //删除热力分公司下的热力所、换热站
                if(this.treeData[0].detail !== undefined && this.treeData[0].detail[typeO].detail !== undefined && this.treeData[0].detail[typeO].detail[typeT].detail !== undefined){
                  for(let typeS = 0;typeS < this.treeData[0].detail[typeO].detail[typeT].detail.length ;typeS++) {
                    //删除热力所下的换热站
                    if (this.treeData[0].detail[typeO].detail[typeT].detail[typeS].type === 3 ) {
                      delete this.treeData[0].detail[typeO].detail[typeT].detail[typeS].detail;
                    }
                    //删除热力公司下的换热站
                    if (this.treeData[0].detail[typeO].detail[typeT].detail[typeS].type === 4) {
                      this.treeData[0].detail[typeO].detail[typeT].detail.splice(typeS, 1);
                      typeS--;
                    }
                  }
                }
              }
            }
          }
        }

      },
      initTreeDataStatus(h, { node, data, store }) {

        if(JSON.stringify(this.treeDataStatus)==="{}"){
          return(
            <span style="font-size:14px;">
            <span>{data.caption }</span>
            </span>
        )
        }else{
          let find = false;
          for (let k of Object.keys(this.treeDataStatus)) {
            if(data.id===k){
              //console.info(k+",显示"+data.caption );
              return(
                <span >
                <span>{data.caption }</span>
                </span>
            )
              find = true;
              break;
            }else{

            }
          }
          if(find === false){
            this.removeTreeNode( node,data);
          }
        }

      },
      initOrgTemplateInfo(){
        const loading = this.$loading({
          lock: true,
          text: '查询中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        let p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postQueryOrganTemplate,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify({config:'all',username:this.customGlobal.userInfoExt.cui_login_id})})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){
            if( this.customGlobal.userInfoExt.cui_type !== '0' ){
              this.treeData = JSON.parse(res.data.organ).detail;
            }else{
              this.treeData =  [JSON.parse(res.data.organ)];
            }
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            this.repackaging();
            loading.close();

          }else{
            console.info("查询失败");
            this.treeData = [ JSON.parse(res.data.organ) ];
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            loading.close();
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();

          this.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }
    }
  }
</script>

